body, html {
}

body {
  max-width: 600px;
  padding: 40px; 
  @media (prefers-color-scheme: dark) {
    background: #0c0c0c;
    color: white;
  }
}



// TYPOGRAPHY


h1 {
  font-size: 100%;
  font-weight: normal;
  margin: 0;
  padding: 0;
}


a { 
  &:visited {
    color: blue;
    @media (prefers-color-scheme: dark) {
      color: rgb(150, 150, 150);
    }
  } 
  @media (prefers-color-scheme: dark) {
    color: rgb(150, 150, 150);
  }
}


// MEDIA

 img {
  max-width: 100%;
  height: auto;
 }



// SECTIONS


.site-header {
  // margin: 50px;
  nav {
    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
      li {
        display: inline-block;
        margin: 0;
        padding: 0;
        &::after {
          content: " |";
          margin-right: .5rem;
        }
      }
    }
  }
}

section {
  margin-bottom: 50px;
}

main {
  margin: 100px 0;
}



.border-right {
  border-right: 1px solid #000;
}














































